header {
    height: 100vh;
    overflow: hidden;
    display: block;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    width: 75%;
    padding-bottom: 5rem;
    margin: 0 auto;
    align-items: center;
}

.welcome__container {
    display: block;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    justify-content: space-between;
}

.header__socials a {
    display: inline-block;
    line-height: 0.6;
}

.me {
    display: block;
    height: 80%;
}

h5 {
    font-size: 1.2rem;
}

/* ============================== MEDIA QUERY (MEDIUM DEVICES) ============================== */

@media screen and (max-width: 1024px) {
    .header__container {
        grid-template-columns: 1fr;
        width: var(--container-width-md);
        gap: 0;
        margin-top: 3rem;
    }

    .me {
        width: 50%;
        margin: 0 auto;
    }

  }
  
  /* ============================== MEDIA QUERY (Small DEVICES) ============================== */
  
  @media screen and (max-width: 600px) {

    .header__container {
        padding-bottom: 0;
        width: var(--container-width-sm);
        height: auto;
        gap: 0.2rem;
    }

    .me {
        width: 80%;
        margin: 0 auto;
    }
  
    }

  /* ============================== MEDIA QUERY (iPSE) ============================== */
  
  @media screen and (max-width: 375px) {

    .me {
        width: 80%;
        margin: 0 auto;
      }
  
    }