.about__container {
    display: grid;
    grid-template-columns: 35% 55%;
    gap: 10%;
    width: 75%;
    margin: 0 auto;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg, 
        transparent, 
        var(--color-primary-variant), 
        transparent
    );
    display: grid;
    place-items: center;   
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0deg);
}

.about__content__skills {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: center;
    margin-top: 0.5rem;
}

.skill__card {
    background: var(--color-primary-variant-opaque);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1.5rem;
    text-align: center;
    width: 9rem;
    height: 9rem;
    transition: var(--transition);
}

.skill__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant-opaque);
    cursor: default;
}

.skill__icon {
    font-size: 2rem;
    margin-bottom: 1rem;;
}

.skill__card h5 {
    font-size: 0.9rem;
}

.about__content__text p {
    margin: 0.5rem 0 1rem;
    color: var(--color-light)   
}

/* ============================== MEDIA QUERY (MEDIUM DEVICES) ============================== */

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 5%;
        width: var(--container-width-md);
    }

    .about__content__text p {
        margin: 1rem 0 1.6rem;
    }

    .skill__card {
        width: 8.5rem;
        height: 8.5rem;
        padding: 1.5rem;
    }

    .skill__icon {
        margin-bottom: 0.5rem;
        
    }

    .about__content__skills {
        gap: 1rem;
    }

    .about__me {
        width: 60%;
        margin: 0 auto;
    }

}   
  
  /* ============================== MEDIA QUERY (Small DEVICES) ============================== */
  
  @media screen and (max-width: 600px) {

    .about__container {
        grid-template-columns: 1fr;
        gap: 5%;
        width: var(--container-width-sm);
    }

    .about__content__text p {
        margin: 1rem 0 1.6rem;
    }

    .skill__card {
        width: 8.5rem;
        height: 8.5rem;
        padding: 1.5rem;
    }

    .skill__icon {
        margin-bottom: 0.5rem;
        
    }

    .about__content__skills {
        gap: 1rem;
    }

    .about__me {
        width: 90%;
        margin: 0 auto;
    }

  }