.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    width: 90%;
    margin: 0 auto;
}

.experience__block {
    background: rgba(255, 255, 255, 0.15);
    border: 1px solid var(--color-primary);
    border-radius: 0 0 2rem 2rem;
    transition: var(--transition);
    height: fit-content;
    display: block;
}

.experience__block__head {
    background-color: var(--color-primary);
    padding: 1rem;
    border: 1px solid var(--color-primary);
    border-radius:  0 0 2rem 2rem;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
}

.experience__block__container {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
}

.experience__subblock {
    /*background-color: rgba(0, 0, 0, 0.20);*/
    border-radius: 2rem;
    transition: var(--transition);
    padding: 1.5rem 1.5rem;
}

/*.experience__block:hover .experience__subblock{
    background: rgba(255, 255, 255, 0.15);
    border-color: var(--color-primary);
    cursor: default;
}*/

.experience__block:hover {
    background: transparent;
    border-color: var(--color-primary);
    cursor: default;

}

.vl {
    border-left: 1px solid white;
}

.hl {
    border-bottom: 1px solid white;
    margin-top: 1rem;
    display: none;
}

.experience__block ul {
    margin-top: 1rem;
    padding-left: 1rem;
    color: var(--color-light);
}

.experience__block ul li {
    margin-top: 0.5rem;
    list-style-type: disc;
}

.experience__block img {
    width: 40%;
    margin: 0 auto;
}

.experience__subblock img {
    width: 35%;
    margin: 0 auto 1rem auto;
}

.experience__block h3 {
    font-size: 1.5rem;
    line-height: 1;
}

.experience__block h4 {
    font-size: 1.2rem;
    text-align: center;
}

.experience__block h5 {
    font-size: 1rem;
    text-align: center;
}

.experience__block p {
    line-height: 1.2;
    margin-top: 0.5rem;
    text-align: center;
}

.bullet {
    margin-right: 0.5rem;
}

.experience__block a {
    text-decoration: underline;
}


/* ============================== MEDIA QUERY (MEDIUM DEVICES) ============================== */

@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
        gap: 2rem;
        width: var(--container-width-md);
    }

    .experience__block {
        width: 100%;
        margin: 0 auto;
    }

    .experience__block img {
        width: 50%;
    }

    .experience__subblock img {
        width: 20%;
    }

    .experience__block__container {
        grid-template-columns: 1fr;
    }

    .hl {
        display: block;
        margin: 0 1rem 0 1rem;
    }

}   
  
  /* ============================== MEDIA QUERY (Small DEVICES) ============================== */
  
  @media screen and (max-width: 600px) {
    .experience__container {
        grid-template-columns: 1fr;
        gap: 2rem;
        width: var(--container-width-sm);
    }

    .experience__block {
        width: 100%;
        margin: 0 auto;
    }

    .experience__block img {
        width: 75%;
    }

    .experience__subblock img {
        width: 35%;
    }

  }