.education__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem ;
    width: 75%;
    margin: 0 auto;
}

.education__block {
    background: rgba(255, 255, 255, 0.15);
    padding: 1.5rem 3rem;
    border: 1px solid transparent;
    border-radius: 2rem;
    transition: var(--transition);
    height: fit-content;
}

.education__subblock {
    background-color: rgba(0, 0, 0, 0.20);
    margin-top: 1rem;
    border-radius: 2rem;
    transition: var(--transition);
    padding: 1rem 1.5rem;
}

.education__block:hover .education__subblock{
    background: rgba(255, 255, 255, 0.15);
    border-color: var(--color-primary);
    cursor: default;
}

.education__block:hover {
    background: transparent;
    border-color: var(--color-primary);
    cursor: default;

}

.education__block ul {
    text-align: center;
}

.education__block img {
    width: 35%;
    margin: 0 auto;
    border-radius: 50%;
}

.education__block h3 {
    font-size: 1.5rem;
    line-height: 1;
}

.education__block h4 {
    font-size: 1.2rem;
    text-align: center;
}

.education__block h5 {
    font-size: 1rem;
    text-align: center;
}

.education__block p {
    line-height: 1.2;
    margin-top: 0.5rem;
    color: var(--color-light);
    text-align: center;
}

.education__block li {
    text-decoration: underline;
}


/* ============================== MEDIA QUERY (MEDIUM DEVICES) ============================== */

@media screen and (max-width: 1024px) {
    .education__container {
        grid-template-columns: 1fr;
        gap: 2rem;
        width: var(--container-width-md);
    }

    .education__block {
        width: 100%;
        padding: 2rem;
        margin: 0 auto;
    }

}   
  
  /* ============================== MEDIA QUERY (Small DEVICES) ============================== */
  
  @media screen and (max-width: 600px) {
    .education__container {
        grid-template-columns: 1fr;
        gap: 2rem;
        width: var(--container-width-sm);
    }

    .education__block {
        width: 100%;
        padding: 2rem;
        margin: 0 auto;
    }

  }