.projects__container {
    display: block;
    gap: 2rem;
    width: 75%;
    margin: 0 auto;
}

.swiper-pagination-clickable .swiper-pagination-bullet {
    background: var(--color-primary);
}

.swiper-button-next {
    color: var(--color-primary) !important;
}

.swiper-button-prev {
    color: var(--color-primary) !important;
}

.swiper-pointer-events {
    height: fit-content;
    width: 60%;
}

.swiper {
    width: 60%;
}

.projects__card {
    background: rgba(255, 255, 255, 0.15);
    border: 1px solid transparent;
    border-radius: 2rem;
    transition: var(--transition);
    min-height: 35rem;
    display: block;
    padding: 1.5rem 3rem;
    margin-top: 2rem;
}

.projects__container h3 {
    font-size: 1.5rem;
    line-height: 1;
}

.cases {
    margin-top: 2rem;
}

.projects__block h4 {
    font-size: 1.2rem;
    line-height: normal;
    margin-bottom: 0.5rem;
}

.projects__block h5 {
    font-size: 1rem;
    line-height: 1.2;
}

.projects__block p {
    line-height: 1.2;
    margin-top: 0.5rem;
    color: var(--color-light);
}

.projects__card-img {
}

.projects__card-img img {
    border-radius: 1rem;
    margin-bottom: 1rem;
}

.btn:disabled {
    background-color: rgba(167, 167, 167, 0.3);
    color: rgba(160, 160, 160, 0.8);
    border-color: rgba(118, 118, 118, 0.3);
    margin-top: 0.5rem;
    cursor: initial;
}

/* ============================== MEDIA QUERY (MEDIUM DEVICES) ============================== */

@media screen and (max-width: 1024px) {
    .projects__container {
        width: var(--container-width-md);
    }

    .swiper {
        height: fit-content;
        width: 100%;
    }

}   
  
  /* ============================== MEDIA QUERY (Small DEVICES) ============================== */
  
  @media screen and (max-width: 600px) {
    .projects__container {
        width: var(--container-width-sm);
    }

    .swiper-pointer-events {
    }

  }