@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@500&display=swap');

footer {
    background: var(--color-primary);
    padding: 1rem 0 6rem;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-family: 'Caveat', cursive;
    margin: 0 auto;
    margin-bottom: 3rem;
    font-size: 4rem;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: white;
    padding: 0.7rem;
    border-radius: 0.7rem;
    line-height: 0.6;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    color: var(--color-light);
    font-size: 0.9rem;
}

  /* ============================== MEDIA QUERY (Small DEVICES) ============================== */
  
  @media screen and (max-width: 600px) {
    .permalinks {  
        flex-direction: column;
        gap: 1.2rem;
}

  }